.search-container {
  justify-content: center;
  margin: 20px 0;
  display: flex;
}

#search-input {
  width: 200px;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

#search-button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

#search-button:hover {
  background-color: #0056b3;
}

#weather-data {
  text-align: center;
  margin-top: 20px;
}

#weather-data img {
  width: 50px;
  height: 50px;
}

#search-form {
  width: 100%;
  max-width: 400px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 20px;
  align-items: center;
  padding: .5em;
  display: flex;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

#city-input {
  border: none;
  outline: none;
  flex: 1;
  padding: .5em;
  font-size: 1em;
}

#location-icon, #star-icon {
  width: 24px;
  fill: #999;
  cursor: pointer;
}

#location-icon:hover, #star-icon:hover {
  fill: #666;
}

#favorites-list {
  max-width: 400px;
  background-color: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 20px;
  margin-top: 1em;
  padding: 0;
  list-style-type: none;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

#favorites-list li {
  color: #fff;
  text-align: center;
  background-color: rgba(16, 33, 54, .5);
  border-bottom: 1px solid #ddd;
  border-radius: 20px;
  margin: 0;
  padding: .5em 1em;
  display: inline-block;
}

#favorites-list li:last-child {
  border-bottom: none;
}

#favorites-list li .remove-favorite {
  cursor: pointer;
  color: rgba(255, 255, 255, .5);
  font-size: 1.2em;
  transition: color .3s;
}

#favorites-list li .remove-favorite:hover {
  color: #c00;
}

#favorites-list li .favorite-text {
  font-size: 1em;
  text-decoration: underline;
}

.master-weather-card {
  flex-direction: column;
  align-items: center;
  display: flex;
  position: absolute;
  right: 0;
}

.weather-card {
  width: 100%;
  background-color: #102136;
  border-radius: 35px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  padding: 30px 0 38px;
  display: flex;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .59);
}

.city-name {
  color: rgba(255, 255, 255, .3);
  margin-bottom: 20px;
  font-size: 16px;
  line-height: 1.375;
}

.temperature-card {
  color: #fff;
  margin: 10px 0;
  font-size: 2em;
}

.divide-line {
  margin: 0 10px;
}

.description {
  color: #fff;
}

.min-temp, .max-temp, .humidity {
  color: rgba(255, 255, 255, .3);
}

.min-temp, .max-temp {
  margin-bottom: 0;
}

.min-text, .max-text {
  color: rgba(255, 255, 255, .3);
  flex-direction: column;
  align-items: center;
  display: flex;
}

.min, .max {
  padding: 0 10px;
  display: flex;
}

.min {
  border-right: 2px solid rgba(255, 255, 255, .3);
}

.divide-line {
  height: 40px;
}

.temps-divide {
  display: flex;
}

.button-container {
  margin-top: 10px;
}

.weather-button {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .weather-card {
    width: 492px;
  }
}



